.content {
  img {
    width: 100%;
    margin-bottom: 50px;
    border-radius: $radius;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; } }
  &__block {
    margin-bottom: 50px;
    border-radius: $radius;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; } }
  &__block-flex {
    @include flex(flex-start, normal, row, wrap); }
  &__left {
    width: percentage(8/12);
    @include lg {
      width: 100%;
      margin-bottom: 30px; }
    @include md {
 } }      // margin-bottom: 15px
  &__right {
    width: percentage(4/12);
    padding-left: 15px;
    @include lg {
      width: 100%;
      padding-left: 0; } } }
