.header {
  $this: &;
  position: relative;
  z-index: 10;
  @include lg {
    position: fixed;
    left: 0;
    top: 0;
    right: 0; }
  &__top {
    background-color: $accent-2;
    color: $white;
    height: $header-top-height;
    @include lg {
      display: none; } }
  &__bottom {
    background-color: $white;
    height: $header-bottom-height;
    @include lg {
      height: $header-bottom-height-lg; }
    @include xs {
      height: $header-bottom-height-xs; } }
  &__inner {
    @extend .row;
    padding-top: 15px;
    padding-bottom: 15px;
    @include lg {
      padding-top: 10px;
      padding-bottom: 10px; }
    @include xs {
      padding-top: 5px;
      padding-bottom: 5px; } }
  &__contacts {
    @extend .col-11;
    @include flex(flex-start, center);
    // margin-left: -15px
 }    // margin-right: -15px
  &__contact {
    padding-left: 15px;
    padding-right: 15px;
    width: 20%;
    @include lg {
      width: auto;
      padding-top: 10px;
      padding-bottom: 10px; } }
  &__social {
    @extend .col-1;
    // text-align: right
    @include flex(flex-end, center); }
  &__logo {
    @extend .col-xl-3;
    // @extend .col-sm-3
    // @extend .col-9
    @include flex(flex-start ,center);
    @include lg {
      width: auto;
      flex-grow: 1; } }
  &__nav {
    @extend .col-xl-6;
    @include flex(flex-start, center);
    .nav:not(.nav--dropdown) {
      width: 100%; }
    @include lg {
      position: fixed;
      display: block;
      z-index: 10;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      background-color: rgba($black, .7);
      // transform: translate(-100%, 0)
      visibility: hidden;
      opacity: 0;
      padding: 0;
      @include ready {
        @include tr(transform .3s, opacity .3s, visibility .3s); }
      &.is-active {
        opacity: 1;
        visibility: visible;
        #{$this}__nav-in {
          transform: translate(0, 0); } } } }
  &__nav-in {
    width: 100%;
    @include flex(flex-start, center);
    position: relative;
    @include ready {
      @include tr(transform .3s); }
    @include lg {
      display: block;
      background-color: $white;
      height: 100%;
      // padding: 15px
      padding-top: 40px;
      padding-bottom: 15px;
      overflow: auto;
      width: 300px;
      transform: translate(-100%, 0); } }
  &__btn {
    @extend .col-xl-3;
    // @extend .col-sm-8
    // @extend .col-3
    @include flex(flex-end, center);
    @include lg {
      width: auto; }
    @include xs {
      display: none; } }
  &__burger {
    display: none;
    padding-right: 15px;
    @include lg {
      @include flex(flex-end, center); } }
  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
    display: none;
    @include lg {
      display: block; } }
  &__contacts-mob, &__social-mob {
    display: none;
    padding-top: 15px;
    padding-bottom: 15px;
    @include lg {
      display: block; } }
  &__social-mob {
    padding-left: 15px;
    padding-right: 15px; }
  &__btn-mob {
    display: none;
    @include xs {
      display: block;
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 15px; } } }



.header-contact {
  @include inline-flex(flex-start, center);
  text-transform: uppercase;
  font-weight: 500;
  font-size: $fz-sm;
  a {
    @include inline-flex(flex-start, center);
    @include tr(color .3s);
    @include hover {
      color: $accent-1; } }
  .icon {
    font-size: 1.2em;
    margin-right: .2em; } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  // margin-top: -24px
  position: relative;
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0; }
  span:after {
    top: 5px; }
  span:before {
    bottom: 5px; }
  span {
    // position: relative
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
  // &.is-active span
  //   background-color: transparent
  // &.is-active span:before
  //   transform: rotate(45deg) translate(-1px, 0px)
  // &.is-active span:after
 }  //   transform: rotate(-45deg) translate(6px, -7px)

