.contacts-block {
  $this: &;
  text-align: center;
  background-color: $white;
  box-shadow: 0px 0px 31px 0px rgba(0, 0, 0, 0.15);
  border-radius: $radius;
  padding: 30px 30px 50px;
  @include xl {
    padding: 30px; }
  @include lg {
    padding: 20px; }
  @include md {
    margin-bottom: 25px; }
  &__btn {
    margin-left: auto;
    margin-right: auto;
    max-width: $btn-width; }
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__social {
    margin-bottom: 45px;
    .social {
      margin-top: 10px; }
    @include xl {
      margin-bottom: 35px; }
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; } }


  &--hor {
    padding-left: 0 !important;
    padding-right: 0 !important;
    box-shadow: none;
    border-radius: 0;
    @include md {
      margin-bottom: 0;
      padding-bottom: 0; }
    #{$this}__contacts {
      @include flex(space-between);
      .contact-info-block {
        width: 33.33333%;
        @include notlast {
          border-right: 1px solid  $gray-light; } }
      @include xss {
        display: block;
        .contact-info-block {
          width: auto;
          @include notlast {
            border-right: 0; } } } } } }

.contact-info-block {
  margin-bottom: 50px;
  @include xl {
    margin-bottom: 40px; }
  @include lg {
    margin-bottom: 30px; }
  @include md {
    margin-bottom: 15px; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent-1; } }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__icon {
    color: $white;
    @include flex(center, center);
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: $accent-1;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto; } }


.map {
  position: relative;
  padding-top: 50%;
  background-color: $gray-light;
  overflow: hidden;
  @include xss {
    padding-top: 70%; }
  iframe {
    @include coverdiv;
    z-index: 1; }
  &--lg {
    border-radius: $radius;
    padding-top: 60%; } }

.panorama {
  position: relative;
  padding-top: 60%;
  background-color: $gray-light;
  @include xss {
    padding-top: 70%; }
  iframe {
    @include coverdiv;
    z-index: 1; } }

.contacts-small {
  &__title {
    margin-bottom: 25px;
    font-weight: 700; }
  &__link {
    @extend .title;
    @extend .title--white;
    @extend .title--h2;
    @extend .title--bold;
    // margin-bottom: 10px
    display: inline-block; } }
