small {
  font-size: inherit; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300; }

.title {
  @include css-lock(21, 36, 320, 1399, font-size);
  line-height: 1;
  color: $c-title;
  text-transform: uppercase;
  font-weight: 500;
  display: block;
  &--h2 {
    @include css-lock(18, 24, 320, 1399, font-size); }
  &--h3 {
    @include css-lock(16, 21, 320, 1399, font-size); }
  &--h5 {
    @include css-lock(14, 16, 767, 1399, font-size);
    @include sm {
      font-size: 14px !important; } }
  &--h6 {
    font-size: $fz-sm !important; }
  &--white {}
  &--sm {
    @include css-lock(21, 34, 320, 1399, font-size); }
  &--xl {
    @include css-lock(34, 80, 320, 1399, font-size); }
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--bold {
    font-weight: 700; }
  &--shadow {
    text-shadow: 1.5px 2.598px 5px rgba(0, 0, 0, 0.78); }
  &--accent {
    color: $accent-1; } }
