.nav {
  color: $c-title;
  ul {
    @include flex(space-between, center);
    @include lg {
      display: block; } }
  li {
    @include lg {
      padding: 10px;
      // height: 41px
      border-bottom: 1px solid $c-text; }
    &.has-dropdown {
      position: relative;
      > a {
        @include after {
          @include icon-arrow(7, 7, 1, currentColor, -45);
          position: relative;
          margin-left: 5px;
          display: inline-block;
          top: -.1em;
          @include lg {
            display: none; } } }
      @include lg {
        padding-right: 50px;
        @include after {
          @include icon-arrow(7, 7, 1, currentColor, -45);
          display: inline-block;
          position: absolute;
          top: 15px;
          right: 17px;
          z-index: 2;
          pointer-events: none; }
        @include before {
          height: 42px;
          width: 40px;
          background-color: $accent-2;
          position: absolute;
          top: 0;
          right: 0;
          z-index: 1;
          pointer-events: none; } }
      @include sm {
        @include before {
          height: 39px; } }
      @include hover {
        .nav--dropdown {
          visibility: visible;
          opacity: 1;
          @include lg {
            // display: block
            max-height: none;
            overflow: visible;
            margin-top: 10px; } } } } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent-1; }
    &.is-active {
      color: $accent-1; } } }

.nav--dropdown {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 100%;
  background-color: transparent;
  width: 360px;
  visibility: hidden;
  opacity: 0;
  padding-top: 20px;
  color: $white;
  @include ready {
    @include tr(visibility .3s, opacity .3s, transform .3s, max-height .5s, padding .5s, margin .5s); }
  @include lg {
    position: static;
    width: auto;
    margin-left: -10px;
    margin-right: -50px;
    transform: translate(0, 0);
    color: inherit;
    visibility: visible;
    opacity: 1;
    padding-top: 0;
    max-height: 0;
    overflow: hidden; }
  &.is-active {
    visibility: visible;
    opacity: 1;
    @include lg {
      // overflow: visible
      margin-top: 10px; } }
  li {
    display: block;
    @include lg {
      border-bottom: 0;
      padding: 0; }

    @include notlast {
      border-bottom: 1px solid  #e4a5e4; }
    &:first-child {
      a {
        border-top-left-radius: $radius-sm;
        border-top-right-radius: $radius-sm;
        @include lg {
          border-radius: 0; } } }
    &:last-child {
      a {
        border-bottom-left-radius: $radius-sm;
        border-bottom-right-radius: $radius-sm;
        @include lg {
          border-radius: 0; } } } }
  ul {
    display: block;
    background-color: $accent-1;
    border-radius: $radius-sm;
    position: relative;
    @include lg {
      background-color: $white;
      border-radius: 0; }
    @include before {
      @include arr(16, 12, $accent-1, t);
      position: absolute;
      bottom: 100%;
      left: 10px;
      z-index: 1;
      @include lg {
        display: none; } } }
  a {
    display: block;
    padding: 10px 15px;
    @include tr(color .3s, background-color .3s);
    @include hover {
      background-color: $accent-2;
      color: $white; }
    &.is-active {
      background-color: $accent-2;
      color: $white;
      @include lg {
        color: $accent-1;
        background-color: transparent; } } } }

.nav--footer {
  color: $white;
  ul {
    display: block; }
  li {
    display: block;
    @include notlast {
      margin-bottom: 15px;
      @include md {
        margin-bottom: 10px; } }
    @include lg {
      padding: 0;
      border-bottom: 0; } }
  a {
    @include hover {
      color: $c-text; } } }
