.accordion {}

.accorion-item {
  $this: &;
  border-bottom: 1px solid  $gray-light;
  font-size: 15px;
  @include sm {
    font-size: $fz-sm; }
  &__title {
    display: block;
    text-align: left;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: 500;
    text-transform: uppercase;
    color: $c-title;
    @include tr(color .3s, margin .5s);
    position: relative;
    padding-right: 60px;
    @include sm {
      padding-top: 10px;
      padding-bottom: 10px; }

    &.is-active {
      color: $accent-2;
      margin-bottom: 15px;
      #{$this}__icon {
        &::after {
          display: none; } }
      @include sm {
        margin-bottom: 10px; } }
    @include hover {
      color: $accent-2; } }
  &__icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    z-index: 0;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    right: 0;
    background-color: $accent-2;
    @include sm {
      width: 30px;
      height: 30px; }
    &::before, &::after {
      content: '';
      background-color: $white;
      @include vcenter;
      border-radius: 3px; }
    &::before {
      width: 20px;
      height: 2px;
      @include sm {
        width: 15px; } }
    &::after {
      width: 2px;
      height: 20px;
      @include sm {
        height: 15px; } } }
  &__content {
    max-height: 0;
    overflow: hidden;
    // padding-top: 15px
    // padding-bottom: 15px
    @include ready {
      @include tr(max-height .5s, margin .5s); }
    strong {
      color: $c-title; }
    p {
      @include notlast {
        margin-bottom: 15px;
        @include md {
          margin-bottom: 10px; } } }
    &.is-active {
      margin-bottom: 15px;
      @include sm {
        margin-bottom: 15px; } } } }
