.social {
  li {
    display: inline-block;
    vertical-align: middle;
    // +notlast
    //   margin-right: 15px
    @include notfirst {
        margin-left: 15px; } }
  .icon {
    font-size: 1.25em; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $c-text; } }

  &--lg {
    .icon {
      font-size: 2.1em; } } }
