.team {
  &__btns {
    height: 100%;
    // +flex(space-between, normal, column)
    .slider__wrap {
      height: 100%;
 } }      // flex-grow: 1
  &__descr {
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: $radius;
    box-shadow: 0px 0px 18px 0px rgba(19, 30, 133, 0.09);
    // padding: 20px
    @include flex;
    @include lg {
      height: auto;
 }      // padding-top: 70%
    .member-full {
      position: relative;
      flex: 0 0 100%;
      // bottom: 50px
      // top: 25px
      // left: 0
      // right: 0
      z-index: 1;
      visibility: hidden;
      opacity: 0;
      @include tr(opacity .3s, visibility .3s);
      &.is-active {
        visibility: visible;
        opacity: 1; } } } }

.member {
  @include flex(flex-start, center);
  width: 100%;
  padding: 30px 25px;
  @include tr(box-shadow .3s, background-color .3s);
  @include xl {
    padding: 25px; }
  @include lg {
    padding: 15px; }
  &.is-active {
    border-radius: $radius-sm;
    box-shadow: 0px 0px 18px 0px rgba(19, 30, 133, 0.09);
    background-color: $white; }
  @include hover {
    @extend .member.is-active; }

  &__img {}

  &__content {
    display: block;
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
    @include lg {
      padding-left: 10px;
      padding-right: 10px; } }
  &__title {
    display: block;
    margin-bottom: 10px;
    @include md {
      margin-bottom: 5px; } }
  &__subttl {
    display: block; } }



.member-full {

  padding: 60px;
  @include flex;
  @include xl {
    padding: 50px; }
  @include lg {
    padding: 40px; }
  @include md {
    padding: 30px; }
  @include sm {
    padding: 20px;
    display: block; }
  &__left {
    width: percentage(360/810);
    @include sm {
      width: auto; } }
  &__right {
    width: percentage(450/810);
    @include sm {
      width: auto; } }
  &__img {
    @include cover;
    // padding-top: 140%
    height: 100%;
    @include lg {
      padding-top: 120%; }
    @include sm {
      padding-top: 50%;
      max-width: 50%;
      margin-bottom: 15px; }
    @include xs {
      max-width: none;
      padding-top: 100%; } }
  &__right {
    @include flex(center, normal, column);
    padding-left: 65px;
    @include xl {
      padding-left: 40px; }
    @include lg {
      padding-left: 20px; }
    @include sm {
      padding-left: 0; } }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__meta {
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } } }

