.footer {
  color: $white;
  background-color: $accent-1;
  &__inner {
    padding-top: 115px;
    padding-bottom: 60px;
    @include xl {
      padding-top: 100px;
      padding-bottom: 50px; }
    @include lg {
      padding-top: 30px;
      padding-bottom: 30px; } }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    font-size: 13px;
    @include lg {
      margin-bottom: 15px; } }
  &__nav {
    @include notlast {
      margin-bottom: 20px;
      @include md {
        margin-bottom: 10px; } } }
  &__auth {
    font-size: 13px; }
  &__auth-icon {
    background-color: $black;
    @include inline-flex(center, center);
    margin-right: 10px;
    width: 38px;
    height: 38px;
    vertical-align: top;
    .icon {
      font-size: 21px; } }
  &__auth-title {
    display: inline-block;
    vertical-align: top;
    a {
      @include tr(color .3s);
      text-decoration: underline;
      @include hover {
        color: $c-text; } } }
  &__block {
    @include sm {
      margin-bottom: 15px; } } }
